@import url(https://fonts.googleapis.com/css?family=Lato:400,700);
.box_feat {
  background-color: #fff;
  padding: 165px 30px 20px 30px;
  text-align: center;
  position: relative;
  border-radius: 5px;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.1);
  margin-bottom: 25px;
}
.box_feat h3 {
  text-transform: uppercase;
  font-size: 21px;
  font-size: 1.3125rem;
  margin-top: 25px;
  color: #e74e84;
}
.box_feat#icon_1 {
  background: #fff url(/static/media/icon-home-1.87c69ad9.svg) no-repeat center 45px;
}
.box_feat#icon_2 {
  background: #fff url(/static/media/icon-home-2.b084af9d.svg) no-repeat center 45px;
}
.box_feat#icon_3 {
  background: #fff url(/static/media/icon-home-3.e4ab3692.svg) no-repeat center 45px;
}
.box_feat span {
  position: absolute;
  top: 50%;
  right: -48px;
  margin: -42px 0 0 0;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 2px solid #ededed;
  background: #fff url(/static/media/arrow-gray-1.1a95f094.svg) no-repeat center center;
  display: block;
  z-index: 9;
}
@media (max-width: 991px) {
  .box_feat span {
    /* For less than 991px */
    top: 100%;
    left: 50%;
    margin: -20px 0 0 -35px;
    transform: rotate(90deg);
  }
}

