#reccomended {
  margin: 40px;
}
@media (max-width: 767px) {
  #reccomended {
    /* For less than 767px */
    margin: 0 0 15px 0;
  }
}
#reccomended .item .title {
  position: absolute;
  bottom: 0;
  left: 0;
  text-align: center;
  width: 100%;
  z-index: 9;
}
#reccomended .item .title h4 {
  background-color: #3f4079;
  background-color: #3f4079;
  display: inline-block;
  border-radius: 3px 3px 0 0;
  color: #fff;
  padding: 10px 15px;
  margin: 0;
  font-size: 16px;
  font-size: 1rem;
  opacity: 0;
}
#reccomended .item .title h4 em {
  display: block;
  font-size: 13px;
  font-size: 13px;
  font-size: 0.8125rem;
  font-weight: 300;
  margin-top: 3px;
}
#reccomended .item a {
  position: relative;
  display: block;
}
#reccomended .item a .views {
  position: absolute;
  right: 15px;
  top: 15px;
  background-color: #3f4079;
  background-color: #3f4079;
  color: #fff;
  box-sizing: border-box;
  padding: 3px 6px 4px 5px;
  line-height: 1;
  border-radius: 3px;
  font-weight: 600;
  font-size: 12px;
  font-size: 0.75rem;
  opacity: 0;
}
#reccomended .item a .views i {
  font-size: 16px;
  font-size: 1rem;
  font-weight: normal;
  margin-right: 3px;
  position: relative;
  top: 1px;
}

#reccomended .owl-item {
  opacity: 0.5;
  transform: scale(0.85);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(0.85, 0.85);
  transition: all 0.3s ease-in-out 0s;
  border-radius: 5px;
  overflow: hidden;
}

#reccomended .owl-item.active.center {
  opacity: 1;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: scale(1);
}

#reccomended .owl-item.active.center .item .title h4,
#reccomended .owl-item.active.center .item .views {
  opacity: 1;
}

.owl-theme .owl-dots {
  margin-top: 25px !important;
}

.owl-theme .owl-dots .owl-dot.active span,
.owl-theme .owl-dots .owl-dot:hover span {
  background: #e74e84 !important;
}

#price {
  text-align: center; }

.plan {
  display: inline-block;
  margin: 10px 15px;
  font-family: 'Lato', Arial, sans-serif; }

.plan-inner {
  background: #fff;
  margin: 0 auto;
  min-width: 280px;
  max-width: 100%;
  position: relative; }

.entry-title {
  background: #53CFE9;
  height: 140px;
  position: relative;
  text-align: center;
  color: #fff;
  margin-bottom: 30px; }

.entry-title > h3 {
  background: #20BADA;
  font-size: 20px;
  padding: 5px 0;
  text-transform: uppercase;
  font-weight: 700;
  margin: 0; }

.entry-title .price {
  position: absolute;
  bottom: -25px;
  background: #20BADA;
