.standard .entry-title > h3 {
  background: #3772aa; }

.standard .price {
  background: #3772aa; }

.ultimite .entry-title > h3 {
  background: #DD4B5E; }

.ultimite .entry-title {
  background: #F75C70; }

.ultimite .price {
  background: #DD4B5E; }

