  }
}

#custom-search-input {
  margin: 0;
  top: 0;
  padding: 0;
  width: 650px;
  margin: auto;
  position: relative;
}
@media (max-width: 991px) {
  #custom-search-input {
    /* For less than 991px */
    width: auto;
  }
}
#custom-search-input .search-query {
  width: 100%;
  height: 50px;
  padding-left: 20px;
  border: 0;
  border-radius: 3px;
  box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.35);
  font-weight: 500;
  font-size: 16px;
  font-size: 1rem;
  color: #333;
}
#custom-search-input .search-query:focus {
  outline: none;
}
@media (max-width: 575px) {
  #custom-search-input .search-query {
    /* For less than 575px */
    padding-left: 15px;
  }
}
#custom-search-input ul {
  text-align: center;
  margin-top: 25px;
}
#custom-search-input ul li {
  display: inline-block;
  margin-right: 5px;
  font-size: 13px;
  font-size: 0.8125rem;
  font-weight: 600;
}
#custom-search-input ul li input[type="radio"] {
  display: none;
}
#custom-search-input ul li input[type="radio"]:checked + label, #custom-search-input ul li input[type="radio"]:checked + label:hover {
  background-color: #74d1c6;
  color: #fff;
}
#custom-search-input ul li input[type="radio"]:hover + label {
  background-color: #e74e84;
  color: #fff;
}
#custom-search-input ul li label {
  display: inline-block;
  transition: all 0.3s ease-in-out;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 3px;
  padding: 8px 10px 6px 10px;
  line-height: 1;
  text-shadow: none;
  min-width: 80px;
  text-align: center;
  cursor: pointer;
}
#custom-search-input ul li:last-child {
  margin-right: 0;
}
#custom-search-input input[type='submit'] {
  position: absolute;
  transition: all 0.3s ease-in-out;
  right: 0;
  color: #fff;
  font-weight: 600;
  font-size: 16px;
  font-size: 1rem;
  top: 0;
  border: 0;
  padding: 0 25px;
  height: 50px;
  cursor: pointer;
  outline: none;
  border-radius: 0 3px 3px 0;
  background-color: #74d1c6;
}
#custom-search-input input[type='submit']:hover {
  background-color: #e74e84;
}
@media (max-width: 575px) {
  #custom-search-input input[type='submit'] {
    /* For less than 575px */
    text-indent: -999px;
    background: #74d1c6 url(/static/media/search.3127f144.svg) no-repeat center center;
  }
}
